import * as xhr from 'modules/fetch'
import { select } from 'lib/selectors'
import bind from 'delegate'
import config from 'lib/config'

function updateProvince() {
	let province = select('[id="address.provincia"]')
    let chinaRegion = select('[id="address.chinaRegionCode"]')
    const cnProvince = province.value
    if (chinaRegion) {
        chinaRegion.value = province.value
    }
    
	let city = select('[id="address.townCity"]')
	let district = select('[id="address.district"]')
    let cityParent = document.getElementById("address.townCity").parentElement
    let districtParent = document.getElementById("address.district").parentElement
    if (cnProvince != "") {
        city.innerHTML = null
        xhr.get(`${window.config.webservices.account.address.cn.addcity}${cnProvince}`).then((response) => {
            const items = response
            let headingOption = document.createElement("option")
               headingOption.value = ""
               headingOption.text = config.webservices.account.address.cn.choose
               city.append(headingOption)
            for (const val of items) {
                let option = document.createElement("option")
                option.value = val.code
                option.text = val.name
                city.append(option)
            }
        })
        city.removeAttribute("disabled")
        city.removeAttribute("aria-disabled")
        cityParent.classList.remove('is-disabled')
        city.removeAttribute("tabindex")
    } else {
        city.value = null
        district.value = null
        city.setAttribute("aria-disabled", true)
        district.setAttribute("aria-disabled", true)
        cityParent.classList.add('is-disabled')
        districtParent.classList.add('is-disabled')
        city.setAttribute("tabindex", -1)
        district.setAttribute("tabindex", -1)
    }
    district.innerHTML = null
    city.dispatchEvent(new Event("change"))
    district.dispatchEvent(new Event("change"))
    district.setAttribute("aria-disabled", true)
    districtParent.classList.add('is-disabled')
}
    
function updateCity() {
	let city = select('[id="address.townCity"]')
    let chinaTown = select('[id="address.chinaCityCode"]')
    const cnDistrict = city.value
    if (chinaTown) {
        chinaTown.value = city.value
    }
	let district = select('[id="address.district"]')
    let districtParent = document.getElementById("address.district").parentElement
    if (cnDistrict != "") {
        district.innerHTML = null
        xhr.get(`${window.config.webservices.account.address.cn.adddistrict}${cnDistrict}`).then((response) => {
            const items = response
            let headingOption = document.createElement("option")
               headingOption.value = ""
               headingOption.text = config.webservices.account.address.cn.choose
               district.append(headingOption)
            for (const val of items) {
                let option = document.createElement("option")
                option.value = val.code
                option.text = val.name
                district.append(option)
            }
        })
        district.removeAttribute("disabled")
        district.removeAttribute("aria-disabled")
        districtParent.classList.remove('is-disabled')
        district.removeAttribute("tabindex")
    } else {
        district.value = null
        district.setAttribute("aria-disabled", true)
        districtParent.classList.add('is-disabled')
        district.setAttribute("tabindex", -1)
    }
    district.dispatchEvent(new Event("change"))
}

function updateDistrict() {
    let district = select('[id="address.district"]')
    let chinaDistrict = select('[id="address.chinaDistrictCode"]')
    if (chinaDistrict) {
        chinaDistrict.value = district.value 
    }
}

function init() {
    bind(document.body, '[id="address.provincia"]', "change", updateProvince)
    bind(document.body, '[id="address.townCity"]', "change", updateCity)
    bind(document.body, '[id="address.district"]', "change", updateDistrict)
}

export default {
    init
}